import React, { Component } from 'react';
import { Datas } from '../components/Datas.js';
import '../App.css';

console.log(Datas);
let data= Datas.map((index)=>{ return index;});
console.log(data);
class VocabCards extends Component {

    constructor(props) {
        super(props)
      
        this.state = {
           
           words:{
                    word:"",
                    meaning:"",
                    synonyms:"",
                    antonyms:"",
                    comments:""
                  }
        }
    
    
      }

      handleWord = ( event ) => {
        this.setState({
          word: event.target.value
        })
  }

  handleMeaning = ( event ) => {
    this.setState({
      meaning: event.target.value
    })
}

handleSynonyms = ( event ) => {
  this.setState({
    synonyms: event.target.value
  })
}

handleAntonyms = ( event ) => {
  this.setState({
    antonyms: event.target.value
  })
}

handleComments = ( event ) => {
  this.setState({
    comments: event.target.value
  })
}

handleAdd= event => {
  if(!this.state.word||!this.state.meaning)
  {
    console.log("Word And Meaning mandatory");
    event.preventDefault();
  }
  else
  {
    var newWord = {id: `${data.length+1}`,word: this.state.word,meaning: this.state.meaning,synonyms:this.state.synonyms,antonyms:this.state.antonyms,comments:this.state.comments}
   
      data = data.concat(newWord);
      event.preventDefault();
      console.log(data);
  }
  
}

    render() {

    var wordIndex = 0;

    function writeThingForward() {
    
    console.log(wordIndex);
    if ( wordIndex === data.length-1 ) 
    {
      wordIndex = 0;
      document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
      document.getElementById("containmean").innerHTML = " ";
    }

    else
    {
      document.getElementById("containword").innerHTML = `<h2>${data[++wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
      document.getElementById("containmean").innerHTML = " ";
    }
    console.log(wordIndex);
  }

  function writeThingForwardTen() {
    
    console.log(wordIndex);
    if ( wordIndex >= data.length-1 ) 
    {
      wordIndex = 0;
      document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
      document.getElementById("containmean").innerHTML = " ";
    }

    else
    { wordIndex= wordIndex + 5;
      if ( wordIndex >= data.length-1 ) wordIndex = 0;
      document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
      document.getElementById("containmean").innerHTML = " ";
    }
    console.log(wordIndex);
  }

  function writeThingBack() {
    
    console.log(wordIndex);
    if ( wordIndex === 0 ) 
    {
      wordIndex = data.length-1;
      document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
        document.getElementById("containmean").innerHTML = 
      `
        <h1></h1>
        
      `;
    
    }
    
    else{
      document.getElementById("containword").innerHTML = `<h2>${data[--wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
      document.getElementById("containmean").innerHTML = " ";
        
    }
    console.log(wordIndex);
  }

  function writeThingBackTen() {
    
    console.log(wordIndex);
    if ( wordIndex <= 0 ) 
    {
      wordIndex = data.length-1;
      document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
        document.getElementById("containmean").innerHTML = 
      `
        <h1></h1>
        
      `;
    
    }
    
    else{
      wordIndex= wordIndex -5;
      if ( wordIndex <= 0 ) wordIndex = data.length-1;
      document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
      document.getElementById("containmean").innerHTML = " ";
        
    }
    console.log(wordIndex);
  }

  function writeThingMean() {

    if(document.getElementById("showHide").innerHTML === "SHOW MEANING")
    {
      document.getElementById("showHide").innerHTML = "HIDE MEANING";
      document.getElementById("showHide").className += " active";
      document.getElementById("containmean").innerHTML = `
                                                            <h5>MEANING:</h5>
                                                            <p>${data[wordIndex].meaning}</p>
                                                            <p>Synonyms: ${data[wordIndex].synonyms}</p>
                                                            <p>Antonyms: ${data[wordIndex].antonyms}</p>
                                                          `; 
  
    }
    else
    {
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
      document.getElementById("showHide").classList.remove("active")
      document.getElementById("containmean").innerHTML = 
    `
      <h1></h1>
      
    `;
    
    }


    
  }

  function sortObjAsc(){
    data.sort(function(a, b){
      var nameA=a.word.toLowerCase(), nameB=b.word.toLowerCase()
      if (nameA < nameB) //sort string ascending
          return -1 
      if (nameA > nameB)
          return 1
      return 0 //default return value (no sorting)
  })

  document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
  document.getElementById("containmean").innerHTML = `<p> </p>`;
  document.getElementById("showHide").innerHTML = "SHOW MEANING";
  document.getElementById("containmean").innerHTML = " ";
    
  }

  function sortObjDesc(){
    data.sort(function(a, b){
      var nameA=a.word.toLowerCase(), nameB=b.word.toLowerCase()
      if (nameA < nameB) //sort string ascending
          return -1 
      if (nameA > nameB)
          return 1
      return 0 //default return value (no sorting)
  })
  data.reverse();

  document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
  document.getElementById("containmean").innerHTML = `<p> </p>`;
  document.getElementById("showHide").innerHTML = "SHOW MEANING";
  document.getElementById("containmean").innerHTML = " ";
    
  }

  function writeThingRandom(){

    wordIndex = Math.floor(Math.random()*data.length);
    if(wordIndex!=data.length)
    { 
      document.getElementById("containword").innerHTML = `<h2>${data[wordIndex].word}</h2>`;
      document.getElementById("containmean").innerHTML = `<p> </p>`;
      document.getElementById("showHide").innerHTML = "SHOW MEANING";
      document.getElementById("containmean").innerHTML = " ";
  }
  }
        return (
            <div>
              <h1>VOCABULARY</h1>
              <div className="container1">
                <div className="row">
                  <button className="button" onClick={writeThingBack}>PREVIOUS</button>
                  <button className="button" id="showHide" onClick={writeThingMean} >SHOW MEANING</button>
                  <button className="button" onClick={writeThingForward}>NEXT</button>
                </div>
              
                <div id="containword">
                  <h2>Entice</h2>
                </div>
                <div id="containmean">
                  
                </div>
              
                <div className="row">
                  <button className="button" onClick={writeThingBackTen}> -5 </button>
                  <button className="button" id="showHide" onClick={writeThingRandom}>RANDOM</button>
                  <button className="button" onClick={writeThingForwardTen}> +5 </button>
                </div>
                <div className="row">
                  <button className="button half" onClick={sortObjAsc}>SORT ASCENDING</button>
                  <button className="button half" onClick={sortObjDesc}>SORT DESCENDING</button>
                </div>
              </div>
              
       
            </div>
        )
    }
}

export default VocabCards
